@import "_base.css";

.header {
    grid-area: head;
    background-color: var(--color-grey-light-1);
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.header__logo {
    margin-left: 4rem;
    height: 4.6rem;
    display: block;
}

.search_box {
    display: flex;
    height: 4rem;

}

.search {
    background-color: #fff;
    border-radius: 0.5rem;
    display: flex;
    align-items: center;
    padding-left: 3rem;
    transition: all 0.3s;
    margin-right: 1rem;
    color: #444;

}

.search :focus-within {
    transform: translateY(-2px);
    box-shadow: 0 0.7rem 3rem rgba(var(--color-grey-dark-1), 0.08);
}

.search__field {
    border: none;
    background: none;
    font-family: inherit;
    color: inherit;
    font-size: 1.7rem;
    width: 30rem;
}

.search :focus {
    outline: none;
}

.search ::placeholder {
    color: var(--color-grey-light-3);
}

.search__btn {
    font-weight: 600;
    font-family: inherit;
    border-radius: 0.5rem;
  }

